.contact-btn {
    width: 153px;
    height: 48px;
    border-radius: 24px;
    border: 2px solid #ffffff;
    background-color: transparent;

    &:hover {
        cursor: pointer;
    }

    &.black {
        border-color: #012f34 ;

        .contact {
            color: #012f34;
        }
    }

    .contact {
        color: white;
        font-size: 18px;
        line-height: 18px;
        font-family: 'livvic-semibold';

    }
}