.section3 {
    background-color: #004047;
    display: flex;
    flex-direction: column;
    padding: 140px 24px 165px;

    .header {
        padding: 0;
        text-align: center;
        font-family: "Livvic-bold";
        font-size: 32px;
        line-height: 32px;

        .turquoise {
            color: #79c8c7;
        }

    }

    .quotes {
        display: flex;
        flex-direction: column;
    }
}

@media(min-width: 600px) {
    .section3 {
        padding: 0 165px;
        height: 745px;

        .header {
            padding: 140px 90px 20px;
            // width: 932px;
            // height: 100px;
            font-size: 48px;
            line-height: 48px;
        }

        .quotes {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
    }

}