.footer {
    background-color: #002529;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 65px 0;
    position: relative;

    .logo-nav {
        display: flex;
        flex-direction: column;
        align-items: center;

        .footer-logo-desktop {
            display: none;
        }

        .links {
            margin-top: 25px;

            a {
                text-decoration: none;
                color: inherit;
                font-size: 15px;
                line-height: 25px;
            }

            .home {
                margin-right: 24px;
            }
        }
    }

    .address {
        margin: 24px 0 0;
        opacity: 0.6;
        font-size: 15px;
        line-height: 25px;
        text-align: center;
    }

    .social-rights {
        margin-top: 45px;
        display: flex;
        flex-direction: column;

        .social {
            display: flex;
            align-items: center;
            justify-content: center;

            >*:not(:last-child) {
                margin-right: 16px;
            }
        }

        .rights {
            margin: 16px 0 0;
            opacity: 0.6;
            font-size: 15px;
            line-height: 25px;
        }
    }
}

@media(min-width: 600px) {
    .footer {
        flex-direction: row;
        height: 196px;
        padding: 0 165px;

        .logo-nav {
            align-items: flex-start;

            .footer-logo-desktop {
                display: block;
            }

            .footer-logo-mobile {
                display: none;
            }

            a {
                font-size: 18px;
                line-height: 28px;
            }

            .home {
                margin-right: 40px;
            }
        }

        .address {
            margin-top: 0;
            margin-left: 125px;
            text-align: left;
        }

        .social-rights {
            margin: 0;
            position: absolute;
            right: 165px;

            .social {
                justify-content: flex-end;
            }

            .rights {
                margin-top: 50px;
            }
        }
    }
}