.section2 {
    background-color: #012f34;
    display: flex;
    flex-direction: column;
    padding: 64px 24px 70px;
    position: relative;


    .rectangle {
        width: 50px;
        height: 4px;
        background-color: #f67e7e;
    }

    .build {
        margin-top: 32px;
        width: 240px;
        font-family: "Livvic-bold";
        font-size: 32px;
        line-height: 32px;
    }

    .Advantages-Details {
        margin-top: 55px;
        display: flex;
        flex-direction: column;

        >*:not(:last-child) {
            margin-block-end: 55px;
        }
    }


    .sec2-img {
        position: absolute;
        right: -100px;
        top: 0;
    }

    .white-cover {
        background-color: white;
        height: 244px;
        width: 100px;
        z-index: 5;
        position: absolute;
        right: -100px;
        top: 0;
    }
}


@media(min-width: 600px) {
    .section2 {
        height: 684px;
        display: grid;
        padding: 0;
        grid-template-columns: 165px 445px 1fr 165px;
        grid-template-rows: 144px 1fr;

        .rectangle {
            grid-area: 1/2/2/5;
            align-self: end;
        }

        .build {
            grid-area: 2/2/3/3;
            margin-top: 53px;
            height: 145px;
            width: auto;
            font-size: 48px;
            line-height: 48px;
        }

        .sec2-img {
            top : auto;
            bottom: 0;
        }

        .white-cover {
            bottom: 0;
            top: auto;
        }

        .Advantages-Details {
            grid-area: 2/3/3/4;
    
            >* {
                margin-block-end: 35px;
            }
        }
    }
}