.qoute-container {
    // width: 350px;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    svg {
        position: relative;
        top: 20px;
        z-index: 5;
    }

    .text {
        font-size: 15px;
        line-height: 25px;
        text-align: center;
        position: relative;
        z-index: 10;
        margin: 0;
    }

    .name {
        color: #79c8c7;
        font-family: "Livvic-bold";
        font-size: 18px;
        line-height: 28px;
        margin: 20px 0 0;
    }

    .position {
        font-family: "Livvic-italic";
        font-size: 13px;
        font-weight: 500;
        line-height: 18px;
        margin: 0;
    }

    img {
        margin-top: 16px;
    }
}

@media(min-width: 600px) {
    .qoute-container {
        margin-top: 0;

        .text {
            width: 352px;
            height: 97px;
        }

        .name {
            margin: 27px 0 0;
        }

        img {
            margin-top: 30px;
        }
    }
}