.sectoin1 {
    background-color: #014e56;
    padding: 48px 0 0;
    display: flex;
    flex-direction: column;
    position: relative;

    header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 24px;

        .logo-desktop {
            display: none;
        }

        .logo-mobile {
            display: block;
        }

        .navbar {
            display: none;

            a {
                text-decoration: none;
                color: inherit;
                margin-right: 30px;
                font-size: 18px;
                font-weight: 600;
                line-height: 32px;
            }

        }
    }

    .find-talent {
        padding: 80px 24px 0;
        display: flex;
        flex-direction: column;
        align-items: center;

        .title {
            grid-area: 1/1/3/2;
            font-family: "Livvic-bold";
            font-size: 40px;
            line-height: 40px;
            text-align: center;

            .second-line::before {
                content: "\a";
                white-space: pre;
            }

            .pink {
                color: #f67e7e;
            }
        }

        .rectangle {
            grid-area: 1/2/2/3;
            width: 50px;
            height: 4px;
            background-color: #79c8c7;
            display: none;
        }

        .p {
            margin-top: 17px;
            margin-bottom: 200px;
            font-size: 15px;
            font-weight: 600;
            line-height: 28px;
            text-align: center;
        }
    }

    .img1 {
        position: absolute;
        bottom: 0;
        left: 8px
    }
}

@media(min-width: 600px) {
    .sectoin1 {
        height: 700px;
        padding: 70px 165px 0;

        header {
            align-items: flex-end;
            justify-content: flex-start;

            .logo-mobile {
                display: none;
            }

            .logo-desktop {
                display: block;
                overflow: visible;
            }

            .navbar {
                width: 100%;
                padding-left: 44px;
                display: flex;
                justify-content: space-between;
                align-items: flex-end;

            }

            .hamburger {
                display: none;
            }
        }

        .find-talent {
            margin-top: 90px;
            display: grid;
            grid-template: 90px 110px/650px 1fr;

            .title {
                align-self: end;
                font-size: 100px;
                line-height: 100px;
                text-align: left;
            }

            .rectangle {
                display: block;
            }

            .p {
                grid-area: 2/2/3/3;
                margin: 0 0 6px;
                align-self: end;
                width: 445px;
                line-height: 28px;
                font-size: 18px;
                text-align: start;
            }
        }

        .img1 {
            position: absolute;
            left: auto;
            right: 165px;
        }
    }
}