.container-sec2-details {
    display: flex;
    flex-direction: column;
    align-items: center;

    .header {
        margin-top: 16px;
        color: #f67e7e;
        font-family: "Livvic-bold";
        font-size: 18px;
        line-height: 28px;
    }

    .text {
        margin: 8px 0 0;
        opacity: 0.8;
        color: #ffffff;
        font-family: "livvic-semibold";
        font-size: 15px;
        line-height: 25px;
        text-align: center;
    }
}

@media(min-width: 600px) {
    .container-sec2-details {
        display: grid;
        grid-template-columns: 196px 1fr;
        grid-template-rows: 43px 1fr;

        svg {
            grid-area: 1/1/2/2;
            justify-self: end;
            padding-top: 40px;
        }

        .header {
            margin-top: 0;
            grid-area: 1/2/2/3;
            padding-inline-start: 22px;
        }

        .text {
            margin: 0;
            grid-area: 2/2/3/3;
            padding-inline-start: 22px;
            text-align: start;
        }
    }
}