.section4 {
    background-color: #f67e7e;
    padding: 83px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    .header {
        margin-bottom: 23px;
        color: #012f34;
        font-family: "Livvic-bold";
        font-size: 32px;
        line-height: 32px;
        text-align: center;
    }

    svg {
        position: absolute;
        bottom: -21px;
        left: 0;
    }
}

@media(min-width: 600px) {
    .section4 {
        padding: 0 260px;
        height: 200px;
        flex-direction: row;
        justify-content: space-between;

        .header {
            text-align: start;
            font-size: 48px;
            line-height: 48px;
        }

        svg {
            bottom: 0;
        }
    }
}